/* eslint-disable */

import logo from './logo.svg';
import './App.css';
import { useState } from 'react';
import { computeHeadingLevel } from '@testing-library/react';

function App() {

  let post = '강남 우동 맛집';

  let [글제목, 글제목변경] = useState(['남자코트 추천', '강남 우동맛집', '파이썬 독학']); 
  let [좋아요, 좋아요변경] = useState([0, 0, 0]);
  let [modal, setModal] = useState(false);
  let [title, setTitle] = useState(0);
  let [입력값, 입력값변경] = useState('');

  return(
    <div className="App">
      <div className="black-nav">
        <h4>React</h4>
      </div>

        {/* 반복문 - 배열.map(콜백함수) */}
        {
          글제목.map(function(el, i) {
            return(
              <div className='list' key = {i}>
                <h4 onClick = {() => {setModal(!modal); setTitle(i)}}>{ 글제목[i] }<span onClick={(e) => { e.stopPropagation();
                  let copy = [...좋아요];
                  copy[i] = copy[i] + 1;
                  좋아요변경(copy)}
                  }> 👍</span> {좋아요[i]} </h4>
                <p>2월 17일 발행</p>
                <button onClick = {() => {
                  let copy = [...글제목];
                  copy.splice(i, 1);
                  글제목변경(copy)}}>삭제하기</button>
              </div>
            )
          })
        }

        <div>
          <input onChange = {(e) => {입력값변경(); 입력값변경(e.target.value);}}></input>
          <button onClick = {() => {
              let copy = [...글제목];
              copy.unshift(입력값);  
              글제목변경(copy)}}>추가</button>
        </div>


        {/* 조건문 - 삼항연산자 */}
        {
          modal == true ? <Modal title = {title} 글제목변경 = {글제목변경} 글제목 = {글제목}></Modal> : null
        }

    </div>
  )
}

  function Modal(props) {
    return(
      <div className="modal">
        {/* 클릭한 글제목을 모달의 제목에 보내야 한다 */}
        <h4>{props.글제목[props.title]}</h4>
        <p>날짜</p>
        <p>상세내용</p>
        <button onClick = {() => {
          props.글제목변경(['여자코트 추천', '강남 우동맛집', '파이썬 독학']);
            // let copy = props.글제목;
            // copy[0] = '여자코트 추천';
            // //왜 즉시 변경이 안될까?
            // props.글제목변경(copy)
          }}>글수정</button>
    </div>
    )
  }

export default App;